import { instance } from '../api-instance-provider';

// Hm ly danh sch tt c matches
export const getAllMatches = async () => {
    try {
        const response = await instance.get('/admin/match', {
            headers: {
                Authorization: `Bearer ${localStorage.getItem('userToken')}`, // Gi token trong header
            },
        });
        return response?.data?.data; // Tr v data nu API tr v ng nh dng
    } catch (error) {
        throw error.response ? error.response.data : { message: 'An error occurred while fetching users' };
    }
};
