<template>
  <div class="edit-profile-page">
    <h2>Chnh sa trang c nhn</h2>

    <form @submit.prevent="saveProfile">
      <!-- Name -->
      <div class="form-group">
        <label for="name">Tn:</label>
        <input
          type="text"
          id="name"
          v-model="profileData.name"
          placeholder="Nhp tn ca bn"
          required
        />
      </div>

      <!-- Age -->
      <div class="form-group">
        <label for="age">Tui:</label>
        <input
          type="number"
          id="age"
          v-model="profileData.age"
          placeholder="Nhp tui"
          min="1"
          max="120"
          required
        />
      </div>

      <!-- Gender -->
      <div class="form-group">
        <label for="gender">Gii tnh:</label>
        <select id="gender" v-model="profileData.gender" required>
          <option value="MALE">Nam</option>
          <option value="FEMALE">N</option>
          <option value="OTHER">Khc</option>
        </select>
      </div>

      <!-- Bio -->
      <div class="form-group">
        <label for="bio">Tiu s:</label>
        <textarea
          id="bio"
          v-model="profileData.bio"
          placeholder="Gii thiu bn thn..."
          :maxlength="bioMaxLength"
          required
        ></textarea>
        <small class="char-counter">Cn li: {{ remainingBioChars }}</small>
      </div>

      <!-- Phone -->
      <div class="form-group">
        <label for="phone">S in thoi:</label>
        <input
          type="tel"
          id="phone"
          v-model="profileData.phone"
          placeholder="Nhp s in thoi ca bn"
          pattern="[0-9]*"
          maxlength="15"
          required
        />
      </div>

      <!-- Photos -->
      <div class="form-group">
        <label for="photos">nh khc:</label>
        <div class="photos-scroll-container">
          <div
            v-for="(photo, index) in profileData.photos"
            :key="index"
            class="photo-input"
          >
            <div class="photo-wrapper">
              <img
                v-if="photo.url"
                :src="photo.url"
                alt="nh khc"
                class="photo-preview"
              />
              <input
                type="file"
                @change="onFileChangePhoto($event, index)"
                accept="image/*"
              />
            </div>
            <button type="button" @click="removePhoto(index)">Xa</button>
          </div>
        </div>
        <button type="button" @click="addPhoto">Thm nh</button>
      </div>

      <!-- Submit Button -->
      <div class="form-group">
        <button type="submit">Lu thay i</button>
      </div>

      <!-- Error Message -->
      <p v-if="profileError" class="profile-error">{{ profileError }}</p>
    </form>
  </div>
</template>

<script>
import { getMyProfile, updateProfile } from "@/services/viewProfile-service.js"; // Import service

export default {
  data() {
    return {
      profileData: {
        name: "", 
        phone: "", 
        age: "", 
        gender: "", 
        bio: "",
        photos: [], // Sa t 'files' thnh 'photos'
      },
      bioMaxLength: 150, //  di ti a cho tiu s
      profileError: "", // Lu tr li
    };
  },
  computed: {
    remainingBioChars() {
      return this.bioMaxLength - this.profileData.bio.length;
    },
  },
  async mounted() {
    try {
      const profileResponse = await getMyProfile();
      const profile = profileResponse.data; 

      this.profileData = {
        name: profile.name || "",
        phone: profile.phone || "",
        age: profile.age || "",
        gender: profile.gender || "",
        bio: profile.bio || "",
        photos: profile.photos.map((photo) => ({
          url: photo.url,
          file: null, 
        })) || [],
      };
      
    } catch (error) {
      console.error("Error loading profile data:", error);
      this.profileError = "Khng th ti thng tin h s.";
    }
  },
  methods: {
    addPhoto() {
      this.profileData.photos.push({ url: "", file: null });
    },

    onFileChangePhoto(event, index) {
      const file = event.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          this.profileData.photos[index].url = e.target.result;
          this.profileData.photos[index].file = file;
        };
        reader.readAsDataURL(file);
      }
    },

    removePhoto(index) {
      this.profileData.photos.splice(index, 1);
    },

    async saveProfile() {
  try {
    const formData = new FormData();
    
    // Chuyn i cc thng tin profile thnh i tng
    const updateProfileRequest = {
      name: this.profileData.name,
      phone: this.profileData.phone,
      age: this.profileData.age,
      bio: this.profileData.bio,
      gender: this.profileData.gender,
    };
    
    formData.append("updateProfileRequest", JSON.stringify(updateProfileRequest));

    // Gn cc file nh ch khi c
    const filesToUpload = this.profileData.photos.map(photo => photo.file).filter(file => file);
    if (filesToUpload.length > 0) {
      filesToUpload.forEach(file => {
        formData.append("files", file);
      });
    }

    // Gi API cp nht profile
    const response = await updateProfile(
      this.profileData.name,
      this.profileData.phone,
      this.profileData.age,
      this.profileData.bio,
      this.profileData.gender,
      filesToUpload // Truyn mng cc tp nh
    );

    if (response.status === 200) {
      alert("Cp nht thnh cng!");
      this.$router.push("/profile"); // iu hng v trang c nhn
    }
  } catch (error) {
    console.error("Error saving profile:", error);
    this.profileError = " xy ra li khi cp nht thng tin.";
  }
},
  },
};
</script>

<style scoped>
/* Styles cho trang chnh sa profile */
.edit-profile-page {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.avatar-wrapper,
.photo-wrapper {
  display: flex;
  align-items: center;
  gap: 20px;
}

.photo-input {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.photo-input button {
  background-color: #ff4b4b;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 5px 10px;
  cursor: pointer;
}

.photo-preview {
  width: 150px;
  height: 150px;
  border-radius: 8px;
  object-fit: cover;
}

button[type="submit"] {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
}

.photos-scroll-container {
  max-height: 300px;
  overflow-y: auto;
  padding: 10px;
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 8px;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}

.char-counter {
  font-size: 12px;
  color: #666;
  text-align: right;
}

.profile-error {
  color: red;
  text-align: center;
}
</style>