import { jwtDecode } from "jwt-decode"; // Dng th vin jwt-decode  gii m token
import { instance } from "./api-instance-provider"; // Import instance API

// Ly thng tin ngi dng t token
export const getLoggedInUser = () => {
  const token = localStorage.getItem("userToken");
  console.log(" ~ getLoggedInUser ~ token:", token);
  if (token) {
    try {
      // Gii m token  ly thng tin user (vd: userId, email)
      const decodedToken = jwtDecode(token);
      return decodedToken; // Tr v thng tin gii m t token
    } catch (error) {
      console.error("Invalid token:", error);
      return null;
    }
  } else {
    console.error("No token found in localStorage.");
    return null;
  }
};

// Hm ly thng tin profile ca ngi dng
export const getMyProfile = async () => {
  const loggedInUser = getLoggedInUser();

  if (loggedInUser && loggedInUser.userId) {
    try {
      const token = localStorage.getItem("userToken");

      // Gi API  ly profile ca ngi dng
      const response = await instance.get(`/profiles/me`, {
        headers: {
          Authorization: `Bearer ${token}`, // Thm token vo header  xc thc
        },
      });

      if (response.data) {
        console.log(" ~ getMyProfile ~ response.data:", response.data);
        return response.data;
      } else {
        throw new Error("Profile data not found");
      }
    } catch (error) {
      console.error("Error fetching profile:", error);
      throw error;
    }
  } else {
    console.error("No logged-in user found.");
    return null;
  }
};

export const updateProfile = async (name, phone, age, bio, gender, files) => {
  try {
    // To FormData  gi d liu multipart/form-data
    const formData = new FormData();

    // Append chui JSON cho `updateProfileRequest`
    const updateProfileRequest = JSON.stringify({
      name,
      phone,
      age,
      bio,
      gender,
    });
    formData.append("updateProfileRequest", updateProfileRequest);

    // Thm tng file vo FormData
    if (files && files.length > 0) {
      files.forEach((file) => {
        formData.append("files", file);
      });
    }

    // Gi yu cu cp nht h s
    const response = await instance.put("/users/update-profile", formData, {
      headers: {
        "Content-Type": "multipart/form-data",
        Authorization: `Bearer ${localStorage.getItem("userToken")}`, // Bearer token cho Authorization
      },
    });

    return response.data; // Tr v data t server
  } catch (error) {
    throw new Error(
      error.response?.data?.message || "Failed to update profile"
    );
  }
};