import { jwtDecode } from "jwt-decode"; // Import th vin jwt-decode  gii m JWT token
import { instance, Login } from "./api-instance-provider"; // Import `instance`  gi API v `Login` cho URL

// Hm lu token, email, role v userId vo localStorage
const saveTokenAndUserInfo = (token, email, role) => {
  if (token) {
    localStorage.setItem("userToken", token);
    localStorage.setItem("email", email);
    localStorage.setItem("role", role);

    // Gii m token  ly userId
    const decodedToken = jwtDecode(token);
    const userId = decodedToken.userId;

    if (userId) {
      localStorage.setItem("userId", userId); // Lu userId vo localStorage
    }
  }
};

// Phng thc x l ng nhp
import { ElNotification } from 'element-plus';

export const loginUser = async (email, password) => {
  try {
    // Gi yu cu ng nhp vi email v password
    const response = await instance.post(Login.ORIGIN, { email, password });
    const { status, message, data } = response.data;

    // Kim tra nu ng nhp thnh cng
    if (status === 200) {
      const { token, role } = data;

      // Lu token v thng tin user vo localStorage
      saveTokenAndUserInfo(token, email, role);

      // X l theo vai tr ca ngi dng
      if (role === "Admin") {
        ElNotification({
          title: 'Success',
          message: 'Admin login successful!',
          type: 'success',
        });
        return "Admin login"; // iu hng ti layout admin
      }
      console.log(message)
      // X l trng hp ng nhp cho User
      if (message.startsWith("First login")) {
        ElNotification({
          title: 'Notice',
          message: 'First login detected, please change your password.',
          type: 'warning',
        });
        return "First login"; // iu hng ti trang i mt khu
      }

      if (message.startsWith("Second login")) {
        ElNotification({
          title: 'Notice',
          message: 'Second login detected, please update your profile.',
          type: 'warning',
        });
        return "Second login"; // iu hng ti trang cp nht h s
      }

      if (message === "Login successful") {
        ElNotification({
          title: 'Success',
          message: 'Login successful!',
          type: 'success',
        });
        return "Login successful"; // iu hng ti trang homePage
      }

      ElNotification({
        title: 'Unknown',
        message: 'Unknown response from server.',
        type: 'error',
      });
      return "Unknown"; // Tr v nu thng bo khng r rng
    }

    // Nu ng nhp tht bi
    ElNotification({
      title: 'Error',
      message: message || 'ng nhp tht bi',
      type: 'error',
    });
    return "Failed"; // Tr v nu ng nhp tht bi

  } catch (error) {
    const status = error?.response?.status;
    const errorMessage = error?.response?.data?.message || "Failed to login";

    // X l li t server (v d: 403 Forbidden)
    if (status === 403) {
      ElNotification({
        title: 'Error',
        message: errorMessage,
        type: 'error',
      });
      return "Error"; // Tr v nu li t server
    }

    // X l li chung trong qu trnh request
    ElNotification({
      title: 'Error',
      message: 'Login failed! Please check your email and password.',
      type: 'error',
    });
    return "Error"; // Tr v nu c li khc
  }
};

