<template>
  <div class="emergency-page-container">
    <!-- Sidebar -->
    <LoveBellSidebar />

    <!-- Ni dung chnh ca trang -->
    <div class="emergency-page">
      <!-- Tiu  trang -->
      <div class="header">
        <h1>GET CURRENT LOCATION</h1>
        <button class="emergency-button" @click="getLocation">Get Location</button>
      </div>
      <p class="mt-4" v-if="location" v-html="location"></p> <!-- S dng v-html  hin th v tr -->

      <!-- Thm bn  -->
      <div id="map" class="map" v-if="latitude && longitude"></div>
    </div>
  </div>
</template>

<script>
import LoveBellSidebar from "@/views/sidebar/LoveBellSidebar.vue";
import L from "leaflet";
import "leaflet/dist/leaflet.css";

export default {
  components: {
    LoveBellSidebar,
  },
  data() {
    return {
      location: null, // Bin  lu thng tin v tr
      latitude: null,  // Lu tr v 
      longitude: null, // Lu tr kinh 
      map: null,      // Bin bn 
      mapInitialized: false, // Bin trng thi  kim tra xem bn   c khi to cha
    };
  },
  methods: {
    async getLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
            (position) => {
              this.showPosition(position);
            },
            (error) => {
              this.handleError(error);
            }
        );
      } else {
        this.location = "Geolocation is not supported by this browser.";
      }
    },
    showPosition(position) {
      this.latitude = position.coords.latitude;
      this.longitude = position.coords.longitude;
      this.location = `Latitude: ${this.latitude}, Longitude: ${this.longitude}`;

      // Gi hm  ly a ch
      this.getAddress(this.latitude, this.longitude);

      this.$nextTick(() => {
        this.initMap(); // Khi to bn  khi c v tr
      });
    },
    async getAddress(lat, lng) {
      try {
        const response = await fetch(`https://nominatim.openstreetmap.org/reverse?lat=${lat}&lon=${lng}&format=json`);
        const data = await response.json();
        if (data && data.display_name) {
          this.location += `<br/> Address: ${data.display_name}`; // Cp nht a ch vo bin location
        }
      } catch (error) {
        console.error("Error fetching address:", error);
      }
    },
    initMap() {
      if (this.mapInitialized) {
        // Nu bn   c khi to, khng cn khi to li
        this.map.setView([this.latitude, this.longitude], 13);
      } else {
        // Khi to bn  ln u tin
        this.map = L.map("map").setView([this.latitude, this.longitude], 13);

        // Thm lp bn  t OpenStreetMap
        L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
          maxZoom: 19,
          attribution: ' OpenStreetMap',
        }).addTo(this.map);

        // Thm marker ti v tr
        L.marker([this.latitude, this.longitude]).addTo(this.map)
            .bindPopup("You are here")
            .openPopup();

        this.mapInitialized = true; // nh du l bn   c khi to
      }
    },
    handleError(error) {
      switch (error.code) {
        case error.PERMISSION_DENIED:
          this.location = "User denied the request for Geolocation.";
          break;
        case error.POSITION_UNAVAILABLE:
          this.location = "Location information is unavailable.";
          break;
        case error.TIMEOUT:
          this.location = "The request to get user location timed out.";
          break;
        case error.UNKNOWN_ERROR:
          this.location = "An unknown error occurred.";
          break;
      }
    },
  },
};
</script>

<style scoped>
/* Container Flexbox  cha sidebar v phn chnh */
.emergency-page-container {
  display: flex;
  align-items: flex-start;
}

.emergency-page {
  flex: 1;
  padding: 20px;
  text-align: center;
  font-family: Arial, sans-serif;
}

.header h1 {
  color: #ff33cc;
  font-size: 48px;
  font-weight: bold;
  margin: 10px 0;
}

.emergency-button {
  background-color: #ff0000;
  border: 2px solid #000;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
}

/* Styling cho bn  */
.map {
  height: 400px; /* Chiu cao ca bn  */
  margin-top: 20px;
}
</style>
