<template>
  <div>
    <div class="sidebarr">
      <!-- Left Sidebar (LoveBellSidebar) -->
      <div class="sidebar-content">
        <LoveBellSidebar />

        <!-- Right Content (Profile Header and Photo Grid) -->
        <div class="content">
          <!-- Profile Header -->
          <div class="profile-header">
            <img
              :src="profileData.avatar"
              alt="Profile Image"
              class="profile-image"
            />
            <div class="profile-info">
              <h2>{{ profileData.name }} - {{ profileData.age }}</h2>
              <h5>{{ profileData.gender }}</h5>
              <p>{{ profileData.bio }}</p>
              
              <button class="edit-profile" @click="showEditProfile = true">
                Chnh sa trang c nhn
              </button>
            
            </div>
           
          </div>

          <!-- Photo Grid -->
          <div class="photo-grid">
            <div
              v-for="(photo, index) in profileData.photos"
              :key="index"
              class="photo-item"
            >
              <img
                :src="photo.url"
                alt="Photo Thumbnail"
                class="photo-thumbnail"
                @click="openPhotoModal(photo.url)"
              />
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal hin th trang chnh sa -->
    <div v-if="showEditProfile" class="modal-overlay">
      <div class="modal-content">
        <!-- Nt X  ng modal -->
        <button class="close-button" @click="showEditProfile = false">X</button>

        <ChangeProfilePage @close="showEditProfile = false" />
      </div>
    </div>

    <!-- Modal hin th nh ln -->
    <div v-if="showPhotoModal" class="modal-overlay">
      <div class="modal-content">
        <button class="close-button" @click="showPhotoModal = false">X</button>
        <img :src="selectedPhoto" alt="Large Photo" class="large-photo" />
      </div>
    </div>
  </div>
</template>
<script>
import LoveBellSidebar from "@/views/sidebar/LoveBellSidebar.vue";
import ChangeProfilePage from "@/views/ChangeProfilePage.vue"; // Import trang chnh sa
import { getMyProfile } from "@/services/viewProfile-service.js"; // Import hm ly profile
// import EventBus from '@/services/event-bus.js';
export default {
  data() {
    return {
      profileData: {
        avatar: "", // Avatar URL
        name: "", // Name
        age: "", // Age
        gender: "", // Gender
        bio: "", // Bio
        photos: [], // Photos array
      },
      showEditProfile: false, // Trng thi hin th modal chnh sa
      showPhotoModal: false, // Trng thi hin th modal nh ln
      selectedPhoto: "", // nh c chn  hin th ln
    };
  },
  components: {
    LoveBellSidebar,
    ChangeProfilePage, // ng k component trang chnh sa
  },
  async mounted() {
    try {
      // Gi API ly thng tin profile ngi dng
      const profileResponse = await getMyProfile();
      const profile = profileResponse.data; // Ly data t response

      // Gn d liu t API vo profileData
      this.profileData.avatar = profile.avatar || "Unnamed User";
      this.profileData.name = profile.name || "Unnamed User";
      this.profileData.age = profile.age || "Unknown";
      this.profileData.gender = profile.gender || "Unknown";
      this.profileData.bio = profile.bio || "No bio available";
      this.profileData.photos = profile.photos || [];
      
    } catch (error) {
      console.error("Error loading profile data:", error);
    }
  },
  methods: {
    openPhotoModal(photoUrl) {
      this.selectedPhoto = photoUrl;
      this.showPhotoModal = true;
    },
  },
};
</script>
<style>
/* Cc style khng thay i */
.sidebarr {
  flex-direction: column;
  height: 100vh;
}

.edit-profile {
  position: fixed; /* V tr c nh cho mn hnh ln */
  right: 30px;
  top: 100px;
  z-index: 100;
}

/* CSS cho mn hnh nh hn */
@media (max-width: 1500px) {
  .edit-profile {
    position: static; /* i sang v tr static  nt nm trong dng chy t nhin ca phn t */
    margin-top: 20px; /* Thm khong cch pha trn nt */
    display: block; /* m bo nt hin th di dng block */
    width: 100%; /* Cho nt chim ton b chiu rng */
    text-align: center; /* Cn gia vn bn trong nt */
  }
}

.sidebar-content {
  display: flex;
  width: 100%;
  height: 100%;
}

.content {
  flex: 5;
  padding-left: 50px;
  padding-top: 50px;
}

/* Profile Header */
.profile-header {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  padding-left: 70px;
}

.profile-image {
  width: 170px;
  height: 170px;
  border-radius: 50%;
  margin-right: 20px;
}

.profile-info {
  font-size: x-large;
}

/* Photo Grid */
.photo-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Chia nh thnh 3 ct */
  gap: 16px; /* Tng khong cch gia cc nh */
  max-height: 650px; /* Gii hn chiu cao */
  overflow-y: auto; /* Thm thanh cun dc khi vt qu chiu cao */
  padding: 0 70px; /* Cn gia li nh */
}

.photo-item {
    position: relative;
  overflow: hidden;
  border-radius: 12px;
  transition: transform 0.3s ease;
}
.photo-item:hover {
  transform: scale(1.05);
}
.photo-thumbnail {
  width: 90%;
  height: auto;
  border-radius: 8px;
  object-fit: cover;
  cursor: pointer;
}

.photo-title {
  margin-top: 5px;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #555;
}

/* Style cho modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 12px;
  max-width: 600px;
  width: 100%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  position: relative;
}

.modal-content {
  max-height: 90vh; /* Gii hn chiu cao modal */
  overflow-y: auto; /* Thm thanh cun dc nu ni dung vt qu chiu cao */
}

/* Style cho nt X (close) */
.close-button {
  position: absolute;
  top: 10px;
  right: 15px;
  background-color: transparent;
  border: none;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
}

.close-button:hover {
  color: red;
}

/* nh ln trong modal */
.large-photo {
  max-width: 100%;
  height: auto;
  border-radius: 12px;
}
</style>
