import { instance } from "./api-instance-provider";

// Hm ly danh sch matches cho ngi dng hin ti (da trn thng tin t token)
export const getMatchesForUser = async () => {
  try {
    const token = localStorage.getItem("userToken"); // Ly token t localStorage
    if (!token) {
      throw new Error("Token khng tm thy. Vui lng ng nhp li.");
    }

    const response = await instance.get("matches", {
      headers: {
        Authorization: `Bearer ${token}`, // nh km token vo header
      },
    });
    return response.data.data; // Tr v data nu API tr v ng nh dng
  } catch (error) {
    throw error.response
      ? error.response.data
      : { message: "An error occurred while fetching matches" };
  }
};

// Hm ly danh sch tin nhn theo matchId
export const getMessagesForMatch = async (matchId, token) => {
  try {
    const response = await instance.get(`/messages/match/${matchId}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    });
    return response.data.data; // Tr v danh sch tin nhn
  } catch (error) {
    console.error("Error fetching messages:", error);
    throw new Error("Unable to fetch messages");
  }
};

// Hm gii m JWT m khng cn th vin bn ngoi
function decodeJWT(token) {
  if (!token || typeof token !== "string") {
    console.error("Invalid token:", token);
    return null;
  }

  try {
    const base64Url = token.split(".")[1]; // Ly phn payload t JWT
    const base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
    const jsonPayload = decodeURIComponent(
      atob(base64)
        .split("")
        .map(function (c) {
          return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
        })
        .join("")
    );
    return JSON.parse(jsonPayload); // Tr v payload di dng object
  } catch (error) {
    console.error("Error decoding JWT:", error);
    return null;
  }
}

// Hm gi tin nhn n server
export const sendMessageToMatch = async (matchId, content, token) => {
  try {
    // Gii m token  ly thng tin ngi dng (senderId)
    const decodedToken = decodeJWT(token);
    if (!decodedToken) {
      console.error("Failed to decode token");
      alert("Token khng hp l. Vui lng ng nhp li.");
      return;
    }
    const senderId = decodedToken.userId || decodedToken.id;
    console.log("Sending payload:", { matchId, senderId, content });

    const response = await instance.post(
      "/messages/send",
      {
        matchId,
        senderId,
        content,
      },
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );
    return response.data.data; // Tr v tin nhn  gi
  } catch (error) {
    console.error(
      "Error sending message:",
      error.response ? error.response.data : error.message
    );
    throw new Error("Unable to send message");
  }
};
